html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.disable-scroll {
  position: relative;
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.graph-modal {
  --transition-time: 0.3s;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.graph-modal::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.graph-modal__container {
  position: relative;
  width: 1000px;
  margin: 50px auto;
  display: inline-block;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  cursor: default;
  text-align: left;
  display: none;
}

@media (max-width: 1024px) {
  .graph-modal__container {
    width: 90%;
  }
}

.graph-modal__close {
  position: absolute;
  right: 10px;
  top: 10px;
  border: none;
  padding: 0;
  width: 20px;
  height: 20px;
  background-color: transparent;
  background-image: url("data:image/svg+xml,%3Csvg version='1.1' id='Capa_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 512.001 512.001' style='enable-background:new 0 0 512.001 512.001;' xml:space='preserve'%3E%3Cg%3E%3Cg%3E%3Cpath d='M284.286,256.002L506.143,34.144c7.811-7.811,7.811-20.475,0-28.285c-7.811-7.81-20.475-7.811-28.285,0L256,227.717 L34.143,5.859c-7.811-7.811-20.475-7.811-28.285,0c-7.81,7.811-7.811,20.475,0,28.285l221.857,221.857L5.858,477.859 c-7.811,7.811-7.811,20.475,0,28.285c3.905,3.905,9.024,5.857,14.143,5.857c5.119,0,10.237-1.952,14.143-5.857L256,284.287 l221.857,221.857c3.905,3.905,9.024,5.857,14.143,5.857s10.237-1.952,14.143-5.857c7.811-7.811,7.811-20.475,0-28.285 L284.286,256.002z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E%0A");
  cursor: pointer;
}

.graph-modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.graph-modal__container.graph-modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
  transition: opacity var(--transition-time);
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
  transition: transform var(--transition-time), opacity var(--transition-time);
}


/*--POPUP--*/

.popup {

  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba($color: #000000, $alpha: 0.8);
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  transform: translateY(-30px);
  visibility: hidden;
  overflow-y: auto;
  transition: all 0.5s;

  &.open {
    opacity: 1;
    visibility: visible;
    transform: translateY(0px);
  }

  &__inner {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 15px;
  }

  &__content {
    background-color: #FEF5EA;
    padding: 56px 123px;
    max-width: 700px;
    border-radius: 30px;
    position: relative;


    @include tablet {
      max-width: 650px;
      padding: 25px 20px;
    }

    @include mobile {
      width: 95%;
      padding: 20px 20px;
    }
  }

  &__close {
    position: absolute;
    background-color: transparent;
    background-color: var(--accent-color);
    border: none;
    top: 15px;
    right: 15px;
    width: 50px;
    height: 50px;
    padding: 0;
    cursor: pointer;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;


    svg {
      fill: var(--white);
      transition: all ease 0.8s;
    }

    &:hover {
      svg {
        transform: rotate(180deg);
      }
    }
  }

  &__form {
    text-align: center;
    ;
  }

  &__title {
    font-size: 30px;
    font-weight: 800;
    margin-bottom: 24px;

    @include tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  &__descr {
    margin-bottom: 40px;

    @include tablet {
      margin-bottom: 32px;
    }

    @include mobile {
      margin-bottom: 24px;
    }
  }

  &__data {
    input::placeholder {
      font: normal 16px/1.5;
      color: rgba(var(--text-color), .8);
      text-decoration: none;
      transition: all 0.5s;



      @include tablet {
        font-size: 14px;
      }

      @include mobile {
        font-size: 13px;
      }
    }

    input:focus::placeholder {
      transition: all 1s ease;
      opacity: 0;
    }
  }

  &__field {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 6px;
    width: 100%;
  }

  &__input {
    height: 60px;
    background-color: var(--white);
    border-radius: 30px;
    border: solid 1px var(--accent-color);
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 24px;
    padding-left: 20px;
    font-size: 16px;
    font-weight: 400;
    color: rgba(var(--text-color), 1);

    &:focus {
      outline: 0;
      border-color: var(--accent-color);
    }

    @include tablet {
      height: 44px;
      margin-bottom: 10px;
      padding-left: 10px;
      font-size: 14px;
    }
  }


  &__agree {
    margin-bottom: 32px;

    .custom-checkbox {
      width: 100%;
    }

    span {
      padding-left: 6px;
    }

    a {
      color: var(--accent-color);
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__btn {
    margin-bottom: 0;

  }
}

.popup__checkbox.just-validate-error-field {
  outline: 1px solid rgb(184, 17, 17);
}
