html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  line-height: normal;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.h2 {
  font-size: 60px;
  font-weight: 800;
  line-height: 1;
  margin: 0;
  color: rgba(var(--text-color), 1);

  @include screen-lg{
    font-size: 46px;
  }
  @include screen-md{
    font-size: 32px;
  }
  @include mobile{
    font-size: 24px;
  }
}

.custom-text {
  p {
    font-size: 19px;
    font-weight: 500;
    line-height: 2;
    color: rgba(var(--text-color), .8);

    @include mobile {
      font-size: 17px;
      line-height: 1.5;
    }
  }

  ul {
    padding-left: 20px;
  }

  li {
    font-size: 19px;
    font-weight: 500;
    line-height: 1.6;
    color: rgba(var(--text-color), .8);

    &:not(:last-child) {
      margin-bottom: 14px;
    }
  }
}
